body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #000;
  background-image: radial-gradient(circle at top right, rgba(121, 68, 154, 0.13), transparent),
    radial-gradient(circle at 20% 80%, rgba(41, 196, 255, 0.13), transparent);
}

.app-container {
  text-align: center;
  color: white;
  padding: 2rem;
}

h1 {
  font-size: 3rem; /* Doubled font size */
  margin-bottom: 1.5rem;
}

.input-container {
  display: flex;
  justify-content: center;
  margin-bottom: 1rem;
}

input[type="text"], input[type="date"] {
  padding: 1rem; /* Doubled size */
  margin: 0 1rem;
  font-size: 1.5rem; /* Doubled font size */
}

.add-button, .delete-all-button, .delete-button, .arrow-button {
  padding: 1rem; /* Doubled size */
  font-size: 1.5rem; /* Doubled font size */
  margin: 0 0.5rem;
  border: none;
  cursor: pointer;
}

.add-button {
  background-color: orange;
  color: white;
}

.delete-all-button, .delete-button {
  background-color: red;
  color: white;
}

.arrow-button {
  background-color: orange;
  color: white;
}

